.graduate-search-form {
	margin-top: 2rem!important;
	font-family: var(--wp--preset--font-family--highlight);
	text-align: center;
	.graduate-search-input {
		background: transparent;
		text-transform: uppercase;
		color: var(--wp--preset--color--primary);
		border: 1px solid var(--wp--preset--color--primary);
		padding: .5rem;
	}
	.search-button {
		text-transform: uppercase;
		background: var(--wp--preset--color--primary);
		color: white;
		border: none;
		padding: .5rem 1rem;
		cursor: pointer;
		&:hover {
			background: var(--wp--preset--color--primary-dark);
		}
	}
	.clear-search {
		text-decoration: none;
		text-transform: uppercase;
		margin-left: 1rem;
	}
}

p.graduate-no-result {
	text-align: center!important;
	margin-top: 4rem;
}

.graduates-table {
	width: calc(100% + 10rem);
  margin-left: -5rem;
  padding-top: 3rem;

	@media (max-width: 60rem) {
		width: 100%;
  		margin-left: auto;
	}

	.table-filter-year {
		text-decoration: none;
	}
	thead {
		th {
			text-align: left;
			font-family: var(--wp--preset--font-family--highlight);
			text-transform: uppercase;
		}
	}
	tbody {
		tr.graduate-row {
			td { 
			}
			td.year-cell {
				vertical-align: top;
				padding-top: 1rem;
				font-size: var(--wp--preset--font-size--650);
				width: 5rem;
			}
			td.prize-cell {
				
				padding-top: .85rem;
				padding-bottom: 4rem;
				vertical-align: top;
				
				.graduate-link {
					font-size: var(--wp--preset--font-size--600);
					text-decoration: none;
					color: var(--wp--preset--color--contrast);
					&:hover {
						color: var(--wp--preset--color--primary);
					}
				}

